.Introduction {
  background-color: var(--color-bg-dark);
  text-align: left;
  display: flex;
  min-height: 85vh;
  font-family: var(--font-family-system);
  font-size: 1rem;
  line-height: 1.8;
  color: var(--color-text-secondary);
  align-items: stretch;
}

.PhotoIntro {
  flex: 1;
  background-color: var(--color-bg-accent);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-xl);
}

.PhotoIntro img {
  max-width: 55%;
  max-height: 55%;
  border: none;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.TextZoneIntro {
  flex: 1;
  padding: 8% 6%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.TextZoneIntro h2 {
  color: var(--color-text-dark);
  font-family: var(--font-family-primary);
  font-size: 3rem;
  font-weight: 400;
  margin-bottom: 0.3rem;
}

.TextZoneIntro b {
  color: var(--color-text-dark);
  font-weight: 600;
}

/* ===================
   CERTIFICATION SECTION
   =================== */
.CertSection {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

.cert-logo {
  width: 80px;
  height: auto;
}

.cert-logo-photo {
  display: block;
  width: 100px;
  height: auto;
  margin-top: var(--spacing-md);
  border: none !important;
}

/* ===================
   RESPONSIVE
   =================== */
@media (max-width: 1100px) {
  .Introduction {
    flex-direction: column-reverse;
    min-height: auto;
  }

  .TextZoneIntro {
    padding: 10% 8%;
    text-align: left;
  }

  .TextZoneIntro h2 {
    font-size: 2.2rem;
  }

  .PhotoIntro {
    padding: var(--spacing-xl);
    min-height: 50vh;
  }

  .PhotoIntro img {
    max-width: 60%;
  }
}
